/**
  * <DynamicOptionList />
  */

import React from 'react';
import ID from './UUID';
import ElementModel from './components/ElementModel';

export default class DynamicOptionList extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      element: this.props.element,
      data: this.props.data,
      dirty: false,
      enableMoreOptions: false,
      current_option_index: -1,
      moreOptionsData: {},
      currentText: "",
      editModeOn: false,
    };
  }

  _setValue(text) {
    return text.replace(/[^A-Z0-9]+/ig, '_').toLowerCase();
  }

  editOption(option_index, e) {
    const this_element = this.state.element;
    const val = e.target.value;//(this_element.options[option_index].value !== this._setValue(this_element.options[option_index].text)) ? this_element.options[option_index].value : this._setValue(e.target.value);

    this_element.options[option_index].text = e.target.value;
    this_element.options[option_index].value = val;
    this.setState({
      element: this_element,
      dirty: true,
    });
  }

  editValue(option_index, e) {
    const this_element = this.state.element;
    const val = (e.target.value === '') ? this._setValue(this_element.options[option_index].text) : e.target.value;
    this_element.options[option_index].value = val;
    this.setState({
      element: this_element,
      dirty: true,
    });
  }

  editScoreValue(option_index, e) {
    const this_element = this.state.element;
    const val = (e.target.value === '') ? '' : e.target.value;
    this_element.options[option_index].score = val;
    this.setState({
      element: this_element,
      dirty: true,
    });
  }

  enableFieldAttribute(option_index, e) {
    const this_element = this.state.element;
    
    this_element.options[option_index].enableField = e.target.checked;
    
    this.setState({ element: this_element });
    this.props.updateElement.call(this.props.preview, this_element);
  }

  fieldTypeOption(option_index, e){
    const this_element = this.state.element;
    
    this_element.options[option_index].fieldType = e.target.value;
    
    this.setState({ element: this_element });
    this.props.updateElement.call(this.props.preview, this_element);
  }

  enableAnotherFormField(option_index, e) {
    const this_element = this.state.element;
    
    this_element.options[option_index].enableAnotherFormField = e.target.checked;
    let enableMoreOptions = e.target.checked;
    let moreOptions = this_element.options[option_index].moreOptions || {};
    let currentText = this_element.options[option_index].text;

    this.setState({ element: this_element, enableMoreOptions: enableMoreOptions, moreOptionsData: moreOptions, currentText: currentText, current_option_index: option_index, editModeOn: false });
    this.props.updateElement.call(this.props.preview, this_element);
  }

  editAnotherFormField(option_index, e) {
    e.preventDefault();
    const this_element = this.state.element;    
    let moreOptions = this_element.options[option_index].moreOptions;
    this.setState({ enableMoreOptions: true, moreOptionsData: moreOptions, current_option_index: option_index, editModeOn: true });
  }

  addFormOptionData(formData, e) {
    const this_element = this.state.element;
    
    this_element.options[this.state.current_option_index].moreOptions = formData;
    this.setState({ element: this_element, enableMoreOptions: false, editModeOn: false });
    this.props.updateElement.call(this.props.preview, this_element);
  }

  // eslint-disable-next-line no-unused-vars
  editOptionCorrect(option_index, e) {
    const this_element = this.state.element;
    if (this_element.options[option_index].hasOwnProperty('correct')) {
      delete (this_element.options[option_index].correct);
    } else {
      this_element.options[option_index].correct = true;
    }
    this.setState({ element: this_element });
    this.props.updateElement.call(this.props.preview, this_element);
  }

  updateOption() {
    const this_element = this.state.element;
    // to prevent ajax calls with no change
    if (this.state.dirty) {
      this.props.updateElement.call(this.props.preview, this_element);
      this.setState({ dirty: false });
    }
  }

  addOption(index) {
    const this_element = this.state.element;
    this_element.options.splice(index + 1, 0, { value: '', text: '', key: ID.uuid() });
    this.props.updateElement.call(this.props.preview, this_element);
  }

  removeOption(index) {
    const this_element = this.state.element;
    this_element.options.splice(index, 1);
    this.props.updateElement.call(this.props.preview, this_element);
  }

  render() {
    if (this.state.dirty) {
       /*let this_element = this.state.element;
       this_element.dirty = true;
       this.state.element = this_element;*/
       //this.setState({element: this_element})
    }
    
    return (
      <div className="dynamic-option-list">
        <ul>
          <li>
            <div className="row">
              <div className="col-sm-4"><b>Options</b></div>
              { /* this.props.canHaveOptionValue &&
              <div className="col-sm-2"><b>Value</b></div> */}
              {  this.props.score &&
              <div className="col-sm-2"><b>Score</b></div> }
              {  /* this.props.canHaveOptionValue && this.props.canHaveOptionCorrect &&
              <div className="col-sm-4"><b>Correct</b></div> */}
              {this.props.enableAnotherForm && 
              <div className="col-sm-3"><b>More Options</b></div> }
              { this.props.element.enableFieldAttribute && <div className="col-sm-2"><b>Enable Field</b></div> } 
              { this.props.element.fieldType && <div className="col-sm-2"><b>Field Type</b></div> } 
            </div>
          </li>
          {
            this.props.element.options.map((option, index) => {
              const this_key = `edit_${option.key}`;
              
              const score = option.score !== undefined ? option.score : '';
              const enableAnotherFormField = option.enableAnotherFormField !== undefined ? option.enableAnotherFormField : false;
              const enableFieldAttribute = option.enableField !== undefined ? option.enableField : false;
              return (
                <li className="clearfix" key={this_key}>
                  <div className="row">
                    <div className="col-sm-4">
                      <input tabIndex={index + 1} className="form-control" style={{ width: '100%' }} type="text" name={`text_${index}`} placeholder="Option text" value={option.text} onBlur={this.updateOption.bind(this)} onChange={this.editOption.bind(this, index)} />
                    </div>
                    {/* this.props.canHaveOptionValue &&
                    <div className="col-sm-2">
                      <input className="form-control" type="text" name={`value_${index}`} readOnly="true" value={val} onChange={this.editValue.bind(this, index)} />
                    </div> */}
                    { this.props.score &&
                    <div className="col-sm-2">
                      <input className="form-control" type="number" name={`score_${index}`} value={score} onChange={this.editScoreValue.bind(this, index)} />
                    </div> }
                    { this.props.enableAnotherForm &&
                    <div className="col-sm-2">
                      <input className="form-control" type="checkbox" value="1" onChange={this.enableAnotherFormField.bind(this, index)} checked={enableAnotherFormField} />
                      {enableAnotherFormField && <button className="btn-edit" onClick = {this.editAnotherFormField.bind(this, index)}><i className="fa fa-pencil"></i></button>}
                    </div> }
                    { this.props.element.enableFieldAttribute &&
                    <div className="col-sm-2">
                      <input className="form-control" type="checkbox" value="1" onChange={this.enableFieldAttribute.bind(this, index)} checked={enableFieldAttribute} />
                      
                    </div> }
                    { this.props.element.fieldType &&
                    <div className="col-sm-3">
                      <select className="form-control" onChange={this.fieldTypeOption.bind(this, index)} name="fieldType" value= {option.fieldType}>
                        <option value="text">Text</option>
                        <option value="number">Number</option>
                        <option value="date">Date</option>
                      </select>
                     
                      
                    </div> }
                    {/* this.props.canHaveOptionValue && this.props.canHaveOptionCorrect &&
                    <div className="col-sm-1">
                      <input className="form-control" type="checkbox" value="1" onChange={this.editOptionCorrect.bind(this, index)} checked={option.hasOwnProperty('correct')} />
                    </div> */}
                    {
                    <div className="col-sm-3">
                      <div className="dynamic-options-actions-buttons">
                        <button onClick={this.addOption.bind(this, index)} className="btn btn-success"><i className="fa fa-plus-circle"></i></button>
                        { index > 0
                          && <button onClick={this.removeOption.bind(this, index)} className="btn btn-danger"><i className="fa fa-minus-circle"></i></button>
                        }
                      </div>
                    </div>
                    }
                  </div>
                </li>
              );
            })
          }
        </ul>
        <ElementModel showModal = {this.state.enableMoreOptions} editModeOn = {this.state.editModeOn} moreOptionsData = {this.state.moreOptionsData} currentText = {this.state.currentText} submitCategory = {this.addFormOptionData.bind(this)} hideModel = {() => { this.setState({enableMoreOptions: false})}} />
      </div>
    );
  }
}
