import React, { Component } from 'react';
import { Col, Row, Button, Input, FormGroup, Label, Modal, ModalHeader, ModalBody, ModalFooter } from 'reactstrap';
//import { toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import './ElementModel.css';
import ID from '../UUID';

class ElementModel extends Component {
  constructor(props){
    super(props);
    this.state = {
      modal: true,
      formType: "",
      labelInfo: "",
      tabularForm: {columns: [{label:"", type:"text", isRequired: false, id: ID.uuid()}], isMultipleRow: true, id: ID.uuid()},
      tabularFormWithComponent: {columns: [{label: "", type: "text", isRequired: false, isSubForm: false, subFormData: [{label:"", type:"text", isRequired: false, id: ID.uuid()}], id: ID.uuid()}], isMultipleRow: true, id: ID.uuid()},
      singleInput: {label: "", type: "text" , id: ID.uuid(), isRequired: false },
      questionComponent: {columns: [{label: "", type: "text", isRequired: false, subFormData: [{label:"", id: ID.uuid()}], id: ID.uuid()}], id: ID.uuid()},
      

    } 
    
    this.submitHandler = this.submitHandler.bind(this);
    
  }

  // Fetch the category List
  componentDidMount() { 
    
  }

  componentWillReceiveProps(nextProps) {
  	if(nextProps.editModeOn){
      let formType = nextProps.moreOptionsData.formType || "";
      let labelInfo = nextProps.moreOptionsData.labelInfo || "";
      let formData = nextProps.moreOptionsData.formData || {};
      if(formType !== "") {
      	if(formType === "tabularForm" ){
      		if(formData.columns !== undefined && Array.isArray(formData.columns))
	      		this.setState({formType: formType, labelInfo: labelInfo, tabularForm: formData, tabularFormWithComponent: {columns: [{label: "", type: "text", isRequired: false, isSubForm: false, subFormData: [{label:"", type:"text", isRequired: false, id: ID.uuid()}], id: ID.uuid()}], isMultipleRow: true, id: ID.uuid()},
	      		singleInput: {label: "", type: "text", id: ID.uuid(), isRequired: false },
	      		questionComponent: {columns: [{label: "", type: "text", isRequired: false, subFormData: [{label:"", id: ID.uuid()}], id: ID.uuid()}], id: ID.uuid()}});
	      	else
	      		this.setState({formType: "", labelInfo: "",
			      tabularForm: {columns: [{label:"", type:"text", isRequired: false, id: ID.uuid()}], isMultipleRow: true, id: ID.uuid()},
			      tabularFormWithComponent: {columns: [{label: "", type: "text", isRequired: false, isSubForm: false, subFormData: [{label:"", type:"text", isRequired: false, id: ID.uuid()}], id: ID.uuid()}], isMultipleRow: true, id: ID.uuid()},
			      singleInput: {label: "", type: "text", id: ID.uuid(), isRequired: false },
			  	  questionComponent: {columns: [{label: "", type: "text", isRequired: false, subFormData: [{label:"", id: ID.uuid()}], id: ID.uuid()}], id: ID.uuid()}});

      	}
      	else if(formType === "tabularFormWithComponent"){
      		if(formData.columns !== undefined && Array.isArray(formData.columns))
	      		this.setState({formType: formType, labelInfo: labelInfo, tabularFormWithComponent: formData, tabularForm: {columns: [{label:"", type:"text", isRequired: false, id: ID.uuid()}], isMultipleRow: true, id: ID.uuid()},
	      		singleInput: {label: "", type: "text", id: ID.uuid(), isRequired: false },
	      		questionComponent: {columns: [{label: "", type: "text", isRequired: false, subFormData: [{label:"", id: ID.uuid()}], id: ID.uuid()}], id: ID.uuid()}});
	      	else
	      		this.setState({formType: "", labelInfo: "",
			      tabularForm: {columns: [{label:"", type:"text", isRequired: false, id: ID.uuid()}], isMultipleRow: true, id: ID.uuid()},
			      tabularFormWithComponent: {columns: [{label: "", type: "text", isRequired: false, isSubForm: false, subFormData: [{label:"", type:"text", isRequired: false, id: ID.uuid()}], id: ID.uuid()}], isMultipleRow: true, id: ID.uuid()},
			      singleInput: {label: "", type: "text", id: ID.uuid(), isRequired: false },
			  	  questionComponent: {columns: [{label: "", type: "text", isRequired: false, subFormData: [{label:"", id: ID.uuid()}], id: ID.uuid()}], id: ID.uuid()}});
      	}
      	else if(formType === "singleInput"){
      		if(formData.label !== undefined)
      			this.setState({formType: formType, labelInfo: labelInfo, singleInput: formData, tabularForm: {columns: [{label:"", type:"text", isRequired: false, id: ID.uuid()}], isMultipleRow: true, id: ID.uuid()},
      			tabularFormWithComponent: {columns: [{label: "", type: "text", isRequired: false, isSubForm: false, subFormData: [{label:"", type:"text", isRequired: false, id: ID.uuid()}], id: ID.uuid()}], isMultipleRow: true, id: ID.uuid()},
      			questionComponent: {columns: [{label: "", type: "text", isRequired: false, subFormData: [{label:"", id: ID.uuid()}], id: ID.uuid()}], id: ID.uuid()}});
      		else
      			this.setState({formType: "", labelInfo: "",
			      tabularForm: {columns: [{label:"", type:"text", isRequired: false, id: ID.uuid()}], isMultipleRow: true, id: ID.uuid()},
			      tabularFormWithComponent: {columns: [{label: "", type: "text", isRequired: false, isSubForm: false, subFormData: [{label:"", type:"text", isRequired: false, id: ID.uuid()}], id: ID.uuid()}], isMultipleRow: true, id: ID.uuid()},
			      singleInput: {label: "", type: "text", id: ID.uuid(), isRequired: false },
			  	  questionComponent: {columns: [{label: "", type: "text", isRequired: false, subFormData: [{label:"", id: ID.uuid()}], id: ID.uuid()}], id: ID.uuid()}});
      	}
      	else if(formType === "questionComponent"){
      		if(formData.columns !== undefined && Array.isArray(formData.columns))
	      		this.setState({formType: formType, labelInfo: labelInfo, tabularFormWithComponent: {columns: [{label: "", type: "text", isRequired: false, isSubForm: false, subFormData: [{label:"", type:"text", isRequired: false, id: ID.uuid()}], id: ID.uuid()}], isMultipleRow: true, id: ID.uuid()},
			    tabularForm: {columns: [{label:"", type:"text", isRequired: false, id: ID.uuid()}], isMultipleRow: true, id: ID.uuid()},
	      		singleInput: {label: "", type: "text", id: ID.uuid(), isRequired: false },
	      		questionComponent: formData});
	      	else
	      		this.setState({formType: "", labelInfo: "",
			      tabularForm: {columns: [{label:"", type:"text", isRequired: false, id: ID.uuid()}], isMultipleRow: true, id: ID.uuid()},
			      tabularFormWithComponent: {columns: [{label: "", type: "text", isRequired: false, isSubForm: false, subFormData: [{label:"", type:"text", isRequired: false, id: ID.uuid()}], id: ID.uuid()}], isMultipleRow: true, id: ID.uuid()},
			      singleInput: {label: "", type: "text", id: ID.uuid(), isRequired: false },
			  	  questionComponent: {columns: [{label: "", type: "text", isRequired: false, subFormData: [{label:"", id: ID.uuid()}], id: ID.uuid()}], id: ID.uuid()}});
      	}
      }
    }
    if(nextProps.currentText !== this.props.currentText)
    	this.setState({formType: "", labelInfo: "",
			      tabularForm: {columns: [{label:"", type:"text", isRequired: false, id: ID.uuid()}], isMultipleRow: true, id: ID.uuid()},
			      tabularFormWithComponent: {columns: [{label: "", type: "text", isRequired: false, isSubForm: false, subFormData: [{label:"", type:"text", isRequired: false, id: ID.uuid()}], id: ID.uuid()}], isMultipleRow: true, id: ID.uuid()},
			      singleInput: {label: "", type: "text", id: ID.uuid(), isRequired: false },
			  	  questionComponent: {columns: [{label: "", type: "text", isRequired: false, subFormData: [{label:"", id: ID.uuid()}], id: ID.uuid()}], id: ID.uuid()}
			  	});
  }
  
  
  submitHandler (event) {
    event.preventDefault();
    event.target.className += " was-validated";
    let formType = this.state.formType;
    let formData = this.state[formType];
    this.props.submitCategory({formType: formType, formData: formData, labelInfo: this.state.labelInfo});
    
  };
  /* Input Field On changes*/
  changeHandler = event => {
    this.setState({[event.target.name]: event.target.value})
  };
  
  toggle = () => {
    this.setState({selectedCategory: []});
    this.props.hideModel();
  }

  addMoreColumns = event => {
  	let tabularForm = this.state.tabularForm;
  	tabularForm['columns'].push({label:"", type:"text", isRequired: false, id: ID.uuid()});
  	this.setState({tabularForm: tabularForm})
  }

  removeColumns = (index, event) => {
  	let tabularForm = this.state.tabularForm;  
  	tabularForm['columns'].splice(index, 1);
  	this.setState({tabularForm: tabularForm});
  }

  onChangeMultipleRowEvent = event => {
  	let tabularForm = this.state.tabularForm;
  	tabularForm['isMultipleRow'] = event.target.checked;
  	this.setState({tabularForm: tabularForm});

  }

  tabularFormChangeHandler = event => {
  	let tabularForm = this.state.tabularForm;  	
  	let name = event.target.name;
    let formName = name.split('_');
    if(formName[0] === "isRequired")
    	tabularForm['columns'][formName[1]]['isRequired'] = event.target.checked;
   	else
   		tabularForm['columns'][formName[1]][formName[0]] = event.target.value;

   	this.setState({tabularForm: tabularForm});
  }

  addMoreColumnsComponent = event => {
  	let tabularFormWithComponent = this.state.tabularFormWithComponent;
  	tabularFormWithComponent['columns'].push({label: "", type: "text", isRequired: false, isSubForm: false, subFormData: [{label:"", type:"text", isRequired: false, id: ID.uuid()}], id: ID.uuid()});
  	this.setState({tabularFormWithComponent: tabularFormWithComponent})
  }

  removeColumnsComponent = (index, event) => {
  	let tabularFormWithComponent = this.state.tabularFormWithComponent;  
  	tabularFormWithComponent['columns'].splice(index, 1);
  	this.setState({tabularFormWithComponent: tabularFormWithComponent});
  }

  onChangeMultipleRowEventComponent = event => {
  	let tabularFormWithComponent = this.state.tabularFormWithComponent;
  	tabularFormWithComponent['isMultipleRow'] = event.target.checked;
  	this.setState({tabularFormWithComponent: tabularFormWithComponent});

  }

  tabularFormWithComponentChangeHandler = event => {
  	let tabularFormWithComponent = this.state.tabularFormWithComponent;  	
  	let name = event.target.name;
    let formName = name.split('_');
    if(formName[0] === "isRequired")
    	tabularFormWithComponent['columns'][formName[1]]['isRequired'] = event.target.checked;
    else if(formName[0] === "isSubForm")
    	tabularFormWithComponent['columns'][formName[1]]['isSubForm'] = event.target.checked;
   	else
   		tabularFormWithComponent['columns'][formName[1]][formName[0]] = event.target.value;

   	this.setState({tabularFormWithComponent: tabularFormWithComponent});
  }

  addMoreSubFormField = (index, event) => {
  	let tabularFormWithComponent = this.state.tabularFormWithComponent;
  	tabularFormWithComponent['columns'][index]['subFormData'].push({label:"", type:"text", isRequired: false, id: ID.uuid()});
  	this.setState({tabularFormWithComponent: tabularFormWithComponent})
  }

  removeSubFormField = (index, subFormIndex, event) => {
  	let tabularFormWithComponent = this.state.tabularFormWithComponent;  
  	tabularFormWithComponent['columns'][index]['subFormData'].splice(subFormIndex, 1);
  	this.setState({tabularFormWithComponent: tabularFormWithComponent});
  }

  onChangeSubFormEvent = event => {
  	let tabularFormWithComponent = this.state.tabularFormWithComponent;  	
  	let name = event.target.name;
    let formName = name.split('_');
    if(formName[0] === "isRequired")
    	tabularFormWithComponent['columns'][formName[1]]['subFormData'][formName[2]]['isRequired'] = event.target.checked;
    else
   		tabularFormWithComponent['columns'][formName[1]]['subFormData'][formName[2]][formName[0]] = event.target.value;

   	this.setState({tabularFormWithComponent: tabularFormWithComponent});
  }

  singleInputComponentChangeHandler = event => {
    let singleInput = this.state.singleInput;
    const name = event.target.name;
    if(name === "isRequired")
      singleInput[name] = event.target.checked;
    else
      singleInput[name] = event.target.value;
    this.setState({singleInput: singleInput});
  	
  }

  addMoreQuestion = event => {
  	let questionComponent = this.state.questionComponent;
  	questionComponent['columns'].push({label: "", type: "text", isRequired: false, subFormData: [{label:"", id: ID.uuid()}], id: ID.uuid()});
  	this.setState({questionComponent: questionComponent})
  }

  removeQuestion = (index, event) => {
  	let questionComponent = this.state.questionComponent;  
  	questionComponent['columns'].splice(index, 1);
  	this.setState({questionComponent: questionComponent});
  }

  questionChangeHandler = event => {
  	let questionComponent = this.state.questionComponent;  	
  	let name = event.target.name;
    let formName = name.split('_');
    if(formName[0] === "isRequired")
    	questionComponent['columns'][formName[1]]['isRequired'] = event.target.checked;
   	else
   		questionComponent['columns'][formName[1]][formName[0]] = event.target.value;

   	this.setState({questionComponent: questionComponent});
  }

  addMoreOptions = (index, event) => {
  	let questionComponent = this.state.questionComponent;
  	questionComponent['columns'][index]['subFormData'].push({label:"", id: ID.uuid()});
  	this.setState({questionComponent: questionComponent})
  }

  removeOptions = (index, subFormIndex, event) => {
  	let questionComponent = this.state.questionComponent;  
  	questionComponent['columns'][index]['subFormData'].splice(subFormIndex, 1);
  	this.setState({questionComponent: questionComponent});
  }

  onChangeOptionsEvent = event => {
  	let questionComponent = this.state.questionComponent;  	
  	let name = event.target.name;
    let formName = name.split('_');
    if(formName[0] === "isRequired")
    	questionComponent['columns'][formName[1]]['subFormData'][formName[2]]['isRequired'] = event.target.checked;
    else
   		questionComponent['columns'][formName[1]]['subFormData'][formName[2]][formName[0]] = event.target.value;

   	this.setState({questionComponent: questionComponent});
  }


  render() {

      
    const modal = this.props.showModal || false;

    return (
      <div className="animated fadeIn">
        
        <Modal isOpen={modal} toggle={this.toggle} className="element-modal-section">
          <ModalHeader toggle={this.toggle}>Choose Services</ModalHeader>
          
            <ModalBody>
            	<FormGroup> 
	                <Label htmlFor="label_info">Headings</Label>            
	                <Input type="text" placeholder="Headings " id="label_info" name="labelInfo" value={this.state.labelInfo} onChange={this.changeHandler} />
	                  
              	</FormGroup>
             	<FormGroup> 
	                <Label htmlFor="formType">Form Component <span className="mandatory">*</span></Label>            
	                <Input type="select" placeholder="Form Type *" id="formType" name="formType" value={this.state.formType} onChange={this.changeHandler} required >
	                  <option value="">Select Form Component</option>
	                  <option value="tabularForm">Create Table Form</option>
	                  <option value="tabularFormWithComponent">Create Table Form With Column Checkboxes</option>
	                  <option value="singleInput">Create Input Field</option>
	                  <option value="questionComponent">Questions</option>
	                </Input>
              	</FormGroup>
              	{
              		this.state.formType === 'tabularForm' ? <TabularFormComponent data = {this.state.tabularForm} onChangeMultipleRowEvent = {this.onChangeMultipleRowEvent} addMoreColumns = {this.addMoreColumns} removeColumns = {this.removeColumns} onChangeEvent = {this.tabularFormChangeHandler} /> : this.state.formType === 'tabularFormWithComponent' ? 
              		<TabularFormWithComponent data = {this.state.tabularFormWithComponent} 
              			onChangeEvent = {this.tabularFormWithComponentChangeHandler} onChangeMultipleRowEventComponent = {this.onChangeMultipleRowEventComponent} 
              			addMoreColumnsComponent = {this.addMoreColumnsComponent} removeColumnsComponent = {this.removeColumnsComponent}
              			onChangeSubFormEvent = {this.onChangeSubFormEvent}
              			addMoreSubFormField = {this.addMoreSubFormField}
              			removeSubFormField = {this.removeSubFormField} /> : this.state.formType === 'singleInput' ? 
              		<SingleInputComponent data = {this.state.singleInput} onChangeEvent = {this.singleInputComponentChangeHandler} /> : this.state.formType === 'questionComponent' ? 
              		<QuestionComponent data = {this.state.questionComponent} onChangeEvent = {this.questionChangeHandler} 
              			addMoreOptions = {this.addMoreOptions} onChangeOptionsEvent = {this.onChangeOptionsEvent}
              			addMoreQuestion = {this.addMoreQuestion} removeOptions = {this.removeOptions} removeQuestion = {this.removeQuestion} /> : <></>

              	}
            </ModalBody>
            <ModalFooter>
              <Button color="primary"  onClick= {this.submitHandler}>Done</Button>
              <Button color="secondary" onClick={this.toggle}>Cancel</Button>
            </ModalFooter>
          
        </Modal>
      </div>

    )
  }
}


function TabularFormComponent(props) {
	return (<>
      <Row>
      	<Col md = {12}>
      		{props.data.columns.map((columnInfo, colIndex) => 
      			<Row className="column-info-rows" key = {colIndex}>
			        <Col md= {4}>
			          <Label>Column Name</Label>
			          <Input type="text" name = {`label_${colIndex}`} value={columnInfo.label} onChange = {props.onChangeEvent} />
			        </Col>
			        <Col md= {4}>
			          <Label>Field Type</Label>
			          <Input type="select" name = {`type_${colIndex}`} value={columnInfo.type} onChange = {props.onChangeEvent} >
			            <option value="">Select Options</option>
			            <option value="text">Text</option>
			            <option value="number">Number</option>
			            <option value="date">Date</option>
			            <option value="checkbox">CheckBox</option>
			          </Input>
			        </Col>
			        <Col md= {2}>
			          <Label>&nbsp;</Label>
			          <Label check>
			            <Input type="checkbox" name = {`isRequired_${colIndex}`} onChange = {props.onChangeEvent} checked = {columnInfo.isRequired !== undefined ? columnInfo.isRequired : false}  value= "yes"  /> Is Required
			          </Label>
			        </Col>
			        <Col md= {2}>
			          <Label>&nbsp;</Label>
			          {
                            colIndex === props.data.columns.length -1 ? 
                            <Col md = {3}>
                              <Button className="edit-btn add-more-icon" color = "success" onClick = {props.addMoreColumns.bind(this)}><i className="fa fa-plus"></i></Button>
                            </Col> : <Col md = {3}><Button className="delete-btn remove-icon" color="danger" onClick = {props.removeColumns.bind(this, colIndex)}><i className="fa fa-minus"></i></Button></Col>
                         }
			        </Col>
			    </Row>
      		)}
      		
	    </Col>
        <Col md= {4}>
          <Label>&nbsp;</Label>
          <Label check>
            <Input type="checkbox" name = "isMultipleRow" onChange = {props.onChangeMultipleRowEvent} checked = {props.data.isMultipleRow !== undefined ? props.data.isMultipleRow : false}  value= "yes"  /> Multiple Rows
          </Label>
        </Col>
      </Row>
  </>)
}
function TabularFormWithComponent(props) {
	return (<>
      <Row>
      	<Col md = {12}>
      		{props.data.columns.map((columnInfo, colIndex) => 
      			<Row className="column-info-rows" key = {colIndex}>
			        <Col md= {3}>
			          <Label>Column Name</Label>
			          <Input type="text" name = {`label_${colIndex}`} value={columnInfo.label} onChange = {props.onChangeEvent} />
			        </Col>
			        <Col md= {3}>
			          <Label>Field Type</Label>
			          <Input type="select" name = {`type_${colIndex}`} value={columnInfo.type} onChange = {props.onChangeEvent} >
			            <option value="">Select Options</option>
			            <option value="text">Text</option>
			            <option value="number">Number</option>
			            <option value="date">Date</option>
			            <option value="checkbox">CheckBox</option>
			          </Input>
			        </Col>
			        <Col md= {3}>
			          <Label>&nbsp;</Label>
			          <Label check>
			            <Input type="checkbox" name = {`isRequired_${colIndex}`} onChange = {props.onChangeEvent} checked = {columnInfo.isRequired !== undefined ? columnInfo.isRequired : false}  value= "yes"  /> Is Required
			          </Label>
			        </Col>
			        <Col md= {3}>
			          <Label>&nbsp;</Label>
			          <Label check>
			            <Input type="checkbox" name = {`isSubForm_${colIndex}`} onChange = {props.onChangeEvent} checked = {columnInfo.isSubForm !== undefined ? columnInfo.isSubForm : false}  value= "yes"  /> Is SubForm
			          </Label>
			        </Col>
			        {columnInfo.isSubForm && <Col md= {12}>
			        		{ columnInfo.subFormData.map((subformInfo, subformIndex) => 
			        			<Row className="subform-component-info-rows" key = {subformIndex}>
			        				<Col md= {4}>
							          <Label>Field Name</Label>
							          <Input type="text" name = {`label_${colIndex}_${subformIndex}`} value={subformInfo.label} onChange = {props.onChangeSubFormEvent} />
							        </Col>
							        <Col md= {4}>
							          <Label>Field Type</Label>
							          <Input type="select" name = {`type_${colIndex}_${subformIndex}`} value={subformInfo.type} onChange = {props.onChangeSubFormEvent} >
							            <option value="">Select Options</option>
							            <option value="text">Text</option>
							            <option value="number">Number</option>
							            <option value="date">Date</option>
							            <option value="checkbox">CheckBox</option>
							          </Input>
							        </Col>
							        <Col md= {2}>
							          <Label>&nbsp;</Label>
							          <Label check>
							            <Input type="checkbox" name = {`isRequired_${colIndex}_${subformIndex}`} onChange = {props.onChangeSubFormEvent} checked = {subformInfo.isRequired !== undefined ? subformInfo.isRequired : false}  value= "yes"  /> Is Required
							          </Label>
							        </Col>
							        <Col md= {2}>
							          <Label>&nbsp;</Label>
							          {
				                            subformIndex === columnInfo.subFormData.length -1 ? 
				                            <Col md = {3}>
				                              <Button className="edit-btn add-more-icon" color = "success" onClick = {props.addMoreSubFormField.bind(this, colIndex)}><i className="fa fa-plus"></i></Button>
				                            </Col> : <Col md = {3}><Button className="delete-btn remove-icon" color="danger" onClick = {props.removeSubFormField.bind(this, colIndex, subformIndex)}><i className="fa fa-minus"></i></Button></Col>
				                         }
							        </Col>
			        			</Row>
			        	    )}
			        	</Col>}
			        <Col md= {2}>
			          <Label>&nbsp;</Label>
			          {
                            colIndex === props.data.columns.length -1 ? 
                            <Col md = {3}>
                              <Button className="edit-btn add-more-icon" color = "success" onClick = {props.addMoreColumnsComponent.bind(this)}><i className="fa fa-plus"></i></Button>
                            </Col> : <Col md = {3}><Button className="delete-btn remove-icon" color="danger" onClick = {props.removeColumnsComponent.bind(this, colIndex)}><i className="fa fa-minus"></i></Button></Col>
                         }
			        </Col>
			    </Row>
      		)}
      		
	    </Col>
        <Col md= {4}>
          <Label>&nbsp;</Label>
          <Label check>
            <Input type="checkbox" name = "isMultipleRow" onChange = {props.onChangeMultipleRowEventComponent} checked = {props.data.isMultipleRow !== undefined ? props.data.isMultipleRow : false}  value= "yes"  /> Multiple Rows
          </Label>
        </Col>
      </Row>
  </>)
}
function SingleInputComponent(props) {
	return (<>
      <Row>
        <Col md= {4}>
          <Label>Field Name</Label>
          <Input type="text" name = "label" value={props.data.label} onChange = {props.onChangeEvent} />
        </Col>
        <Col md= {4}>
          <Label>Field Type</Label>
          <Input type="select" name = "type" value={props.data.type} onChange = {props.onChangeEvent} >
            <option value="">Select Options</option>
            <option value="text">Text</option>
            <option value="number">Number</option>
            <option value="date">Date</option>
            <option value="checkbox">CheckBox</option>
          </Input>
        </Col>
        <Col md= {4}>
          <Label>&nbsp;</Label>
          <Label check>
            <Input type="checkbox" name = "isRequired" onChange = {props.onChangeEvent} checked = {props.data.isRequired !== undefined ? props.data.isRequired : false}  value= "yes"  /> Is Required
          </Label>
        </Col>
      </Row>
  </>)
}
function QuestionComponent(props) {
	return (<>
      <Row>
      	<Col md = {12}>
      		{props.data.columns.map((columnInfo, colIndex) => 
      			<Row className="column-info-rows" key = {colIndex}>
			        <Col md= {6}>
			          <Label>Questions Name</Label>
			          <Input type="text" name = {`label_${colIndex}`} value={columnInfo.label} onChange = {props.onChangeEvent} />
			        </Col>
			        <Col md= {4}>
			          <Label>Field Type</Label>
			          <Input type="select" name = {`type_${colIndex}`} value={columnInfo.type} onChange = {props.onChangeEvent} >
			            <option value="">Select Options</option>
			            <option value="text">Text</option>
			            <option value="number">Number</option>
			            <option value="date">Date</option>
			            <option value="radio">Radio</option>
			            <option value="checkbox">CheckBox</option>
			            <option value="dropdown">DropDown</option>
			          </Input>
			        </Col>
			        <Col md= {2}>
			          <Label>&nbsp;</Label>
			          <Label check>
			            <Input type="checkbox" name = {`isRequired_${colIndex}`} onChange = {props.onChangeEvent} checked = {columnInfo.isRequired !== undefined ? columnInfo.isRequired : false}  value= "yes"  /> Is Required
			          </Label>
			        </Col>
			        {(columnInfo.type === "checkbox" || columnInfo.type === "radio" || columnInfo.type === "dropdown") &&
			        	<Col md= {12}>
			        		<Row className="subform-component-info-rows">
			        		{ columnInfo.subFormData.map((subformInfo, subformIndex) => 			        			
			        				<Col md= {4}  key = {subformIndex}>
							          <Label>Option</Label>
							          <Input type="text" name = {`label_${colIndex}_${subformIndex}`} value={subformInfo.label} onChange = {props.onChangeOptionsEvent} />
							          {
				                            subformIndex === columnInfo.subFormData.length -1 ? 
				                            
				                              <Button className="edit-btn add-more-icon" color = "success" onClick = {props.addMoreOptions.bind(this, colIndex)}><i className="fa fa-plus"></i></Button>
				                             : <Button className="delete-btn remove-icon" color="danger" onClick = {props.removeOptions.bind(this, colIndex, subformIndex)}><i className="fa fa-minus"></i></Button>
				                         }
							        </Col>
			        			
			        	    )}
			        	    </Row>
			        	</Col>
			        }
			        <Col md= {2}>
			          <Label>&nbsp;</Label>
			          {
                            colIndex === props.data.columns.length -1 ? 
                            <Col md = {3}>
                              <Button className="edit-btn add-more-icon" color = "success" onClick = {props.addMoreQuestion.bind(this)}><i className="fa fa-plus"></i></Button>
                            </Col> : <Col md = {3}><Button className="delete-btn remove-icon" color="danger" onClick = {props.removeQuestion.bind(this, colIndex)}><i className="fa fa-minus"></i></Button></Col>
                         }
			        </Col>
			    </Row>
      		)}
      		
	    </Col>
        
      </Row>
  </>)
}
export default ElementModel;
