import React, { Component } from 'react';
import { Button, Input, FormGroup, Label, Modal, ModalHeader, ModalBody, ModalFooter } from 'reactstrap';
import { toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import commonService from '../../../core/services/commonService';
import './CategoryModel.css'

class CategoryModel extends Component {
  constructor(props){
    super(props);
    this.state = {
      modal: true,
      category_name: "",
      categoryList: [],
      selectedCategory: [],
      

    } 
    
    this.submitHandler = this.submitHandler.bind(this);
    this.categoryList = this.categoryList.bind(this);
    
  }

  // Fetch the category List
  componentDidMount() { 
    this.categoryList();
  }
  componentWillReceiveProps(nextProps) {
    
    if(nextProps.showModal){
      this.categoryList();
      this.setState({selectedCategory: []});
    }
  }

  categoryList() {
    
    this.setState( { loading: true}, () => {
      commonService.getAPIWithAccessToken('category/subcategory')
        .then( res => {
          console.log(res);
           
          if ( undefined === res.data.data || !res.data.status ) {
            this.setState( {  loading: false } );
            toast.error(res.data.message);    
            return;
          }   

          this.setState({loading:false, categoryList: res.data.data});     
         
        } )
        .catch( err => {   
                
          if(err.response !== undefined && err.response.status === 401) {
            localStorage.clear();
            this.props.history.push('/login');
          }
          else {
            this.setState( { loading: false } );
            toast.error(err.message);    
          }
        } )
    } )
  }
  
  submitHandler (event) {
    event.preventDefault();
    event.target.className += " was-validated";
    let selectedCategory = this.state.selectedCategory;
    let selectCatInfo = this.state.categoryList.map(function(item) { if(selectedCategory.indexOf(item.categoryId) > -1) { return {catId: item.categoryId, catName: item.categoryName, subCategoryData: item.subCategoryInfo}; } else return {}});
    selectCatInfo = selectCatInfo.filter(value => JSON.stringify(value) !== '{}');
    debugger;
    this.props.submitCategory(selectCatInfo);
    this.setState({selectedCategory: []})
  };
  /* Input Field On changes*/
  changeHandler = event => {
    let selectedCategory = this.state.selectedCategory;
    
    if(event.target.checked){
      if(selectedCategory.indexOf(event.target.value) < 0)
        selectedCategory.push(event.target.value);
    }
    else {
      if(selectedCategory.indexOf(event.target.value) > -1)
        selectedCategory.splice(selectedCategory.indexOf(event.target.value), 1);
    }

    this.setState({selectedCategory: selectedCategory});
  };
  
  toggle = () => {
    this.setState({selectedCategory: []});
    this.props.hideCatModel();
  }



  render() {

    const { categoryList  } = this.state;     
    const modal = this.props.showModal || false;

    return (
      <div className="animated fadeIn">
        
        <Modal isOpen={modal} toggle={this.toggle} className="category-modal-section">
          <ModalHeader toggle={this.toggle}>Choose Services</ModalHeader>
          
            <ModalBody>
             {categoryList.map((categoryInfo, catIndex) => 
               <FormGroup check inline key = {catIndex}>
                  <Label check>
                    <Input type="checkbox" name = "cat[]" onChange = {this.changeHandler} checked = {this.state.selectedCategory.indexOf(categoryInfo.categoryId) > -1 ? true : false} value= {categoryInfo.categoryId}  /> {categoryInfo.categoryName}
                  </Label>
                </FormGroup>
             )}
              
            </ModalBody>
            <ModalFooter>
              <Button color="primary"  onClick= {this.submitHandler}>Done</Button>
              <Button color="secondary" onClick={this.toggle}>Cancel</Button>
            </ModalFooter>
          
        </Modal>
      </div>

    )
  }
}

export default CategoryModel;
